// Typography are defined without font-size because they are already defined via theme.json
.text-heading-3xl {
  @apply leading-[--heading-3xl-line-height] font-[--heading-3xl-font-weight-sm] md:font-[--heading-3xl-font-weight-md] font-[family-name:var(--heading-3xl-font-family)];
}

